main, #header, #subheader, #footer, #content {width:100%}
#content {padding: 0 1.25em}
main        {flex: 1}
#footer     {align-self: end}


/* Media Queries Template 1200px 960px 720px 480px 384px */
@media all and (max-width:75em) {}
@media all and (max-width:60em) {}
@media all and (max-width:45em) {}
@media all and (max-width:30em) {}
@media all and (max-width:24em) {}


.row                {box-sizing:border-box;display:flex;flex:0 1 auto;flex-direction:row;flex-wrap:wrap;}
.column             {flex-direction: column}
.row.reverse        {flex-direction:row-reverse}
.column, .columns   {box-sizing:border-box;flex:0 0 auto;padding-right:0.5rem;padding-left:0.5rem}
.columns.reverse    {flex-direction:column-reverse}

/* ========     FLEX       ========  */
.flex-inline  {display: inline-flex}

.flex-around  {display:flex; justify-content:space-around;}
.flex-between {display:flex; justify-content:space-between;}
.flex-start   {display:flex; justify-content: flex-start}
.flex-center  {display:flex; justify-content:center;}
.flex-end     {display:flex; justify-content: flex-end}
.flex-evenly  {display:flex; justify-content:space-evenly;}

.flex-wrap  {display:flex; flex-wrap: wrap}
.flex-nowrap  {display:flex; flex-wrap: nowrap}

.small              {flex-grow:1;flex-basis:0;max-width:100%}
.small-1            {flex-basis:8.33333333%;max-width:8.33333333%}
.small-2            {flex-basis:16.66666667%;max-width:16.66666667%}
.small-3            {flex-basis:25%;max-width:25%}
.small-4            {flex-basis:33.33333333%;max-width:33.33333333%}
.small-5            {flex-basis:41.66666667%;max-width:41.66666667%}
.small-6            {flex-basis:50%;max-width:50%}
.small-7            {flex-basis:58.33333333%;max-width:58.33333333%}
.small-8            {flex-basis:66.66666667%;max-width:66.66666667%}
.small-9            {flex-basis:75%;max-width:75%}
.small-10           {flex-basis:83.33333333%;max-width:83.33333333%}
.small-11           {flex-basis:91.66666667%;max-width:91.66666667%}
.small-12           {flex-basis:100%;max-width:100%}
.small-offset-0     {margin-left:0}
.small-offset-1     {margin-left:8.33333333%}
.small-offset-2     {margin-left:16.66666667%}
.small-offset-3     {margin-left:25%}
.small-offset-4     {margin-left:33.33333333%}
.small-offset-5     {margin-left:41.66666667%}
.small-offset-6     {margin-left:50%}
.small-offset-7     {margin-left:58.33333333%}
.small-offset-8     {margin-left:66.66666667%}
.small-offset-9     {margin-left:75%}
.small-offset-10    {margin-left:83.33333333%}
.small-offset-11    {margin-left:91.66666667%}
.small-start        {justify-content:flex-start;text-align:start}
.small-center       {justify-content:center;text-align:center}
.small-end          {justify-content:flex-end;text-align:end}
.small-top          {align-items:flex-start}
.small-middle       {align-items:center}
.small-bottom       {align-items:flex-end}
.small-around       {justify-content:space-around}
.small-between      {justify-content:space-between}
.small-first        {order:-1}
.small-last         {order:1}
.small-hide         {display: none}  .medium-hide, .large-hide  {display: inherit}

.small-p1     {padding:.5rem}
.small-p2     {padding:1rem}
.small-p3     {padding:1.5rem}
.small-pt1    {padding-top:.5rem}
.small-pt2    {padding-top:1rem}
.small-pt3    {padding-top:1.5rem}
.small-pr1    {padding-right:.5rem}
.small-pr2    {padding-right:1rem}
.small-pr3    {padding-right:1.5rem}
.small-pb1    {padding-bottom:.5rem}
.small-pb2    {padding-bottom:1rem}
.small-pb3    {padding-bottom:1.5rem}
.small-pl1    {padding-left:.5rem}
.small-pl2    {padding-left:1rem}
.small-pl3    {padding-left:1.5rem}

.small-m1     {margin:.5rem}
.small-m2     {margin:1rem}
.small-m3     {margin:1.5rem}
.small-mt1    {margin-top:.5rem}
.small-mt2    {margin-top:1rem}
.small-mt3    {margin-top:1.5rem}
.small-mr1    {margin-right:.5rem}
.small-mr2    {margin-right:1rem}
.small-mr3    {margin-right:1.5rem}
.small-mb1    {margin-bottom:.5rem}
.small-mb2    {margin-bottom:1rem}
.small-mb3    {margin-bottom:1.5rem}
.small-ml1    {margin-left:.5rem}
.small-ml2    {margin-left:1rem}
.small-ml3    {margin-left:1.5rem}

