nav[role=tablist]
{position: relative;display: flex;align-self: center; margin: 0 10%}

nav[role=tablist] a
{position: relative;z-index: 1;text-align: center;flex: 1;text-decoration: none;white-space: nowrap;
padding: .5rem 1.75rem; color: #fff; background-color: var(--primary-background-color);flex-direction: row-reverse; border-radius: .3em .3em 0 0}

nav[role=tablist] a[aria-selected=true]
{background-color: var(--primary-background-color); color:#000; border-bottom: 0;}

nav[role="tablist"] a[aria-hidden="false"] {color: #000}

nav[role="tablist"] a[aria-selected="true"] {}

section[role="tabpanel"], nav[role="tablist"] a:focus {outline: none}

section[role="tabpanel"] {display: none;}

section[aria-hidden="false"] {display: block}


.tab-badge, .num-badge, .num-badge-red
       {background-color: var(--primary-color); width: 2.7em; color: #fff; border-radius: 1em;margin: 0 auto;font-size: .8em;padding: .2em .4em;}
.num-badge
       {background-color: var(--secondary-color);}
.num-badge-red
       {background-color: darkred;}