#header                       {display:flex;justify-content:space-around;place-items:center;flex-flow:row wrap;position:relative}
#header nav                   {display:flex;justify-content:center;flex-wrap:wrap;place-items:center}
#header nav a                 {display:flex;flex-direction:column-reverse;line-height:1rem;text-align:center;padding:15px;border-bottom:solid 2px transparent;
                              text-transform: capitalize; font-size: clamp(.8rem, 2vw, 1rem); color:#222;}
#header nav a:hover           {color:var(--third-color);}
#header nav a.selected        {border-bottom-color:#fff;}
#header nav a .icon           {display:block; margin:0 auto .25em; font-size:1.5em; width:1.5em;height:1em;vertical-align:-.15em}
#header nav a.nav-wide svg    {width:2.5em; height:1em;}
#logo                         {display:flex;flex-direction:row;place-items:center;justify-content:center}





