.text-center {
  text-align: center;
}

h1,h2,h3,h4,h5,h6 {
  text-align: center;
}

h1 {
  font-size: clamp(1.5rem, 5vw, 2rem);
  line-height: clamp(1.7rem, 5vw, 2.5rem);
}

h2 {
  font-size: clamp(1.3rem, 4vw, 1.7rem);
  line-height: clamp(1.6rem, 4vw, 2rem);
}

h3 {
  font-size: clamp(1.2rem, 2vw, 1.5rem);
  line-height: clamp(1.4rem, 2vw, 1.8rem);
}

h4 {
  font-size: clamp(1rem, 4vw, 1.3rem);
  line-height: clamp(1.2rem, 4vw, 1.5rem);
  font-weight: 400;
}

p {
  font-size: clamp(.82rem, 2vw, 1rem);
  line-height: clamp(.96rem, 2vw, 1.25rem);
  margin-bottom: clamp(.2rem, 2.5vw, .4rem);
}

nav {
  font-size: clamp(.8rem, 3vw, 1.1rem);
}

h1 small,h2 small,h3 small,h4 small,h5 small,h6 small {
  display: block;
  font-size: .5em;
}

main > h1 {
  padding: 1rem 0 2rem;
}

.smaller {font-size: smaller}
.larger   {font-size: larger}

blockquote  {
  padding-inline: clamp(2.25em, 4vw, 3.25em);
  font-size: clamp(.8em, 1.6vw, 1.4em);
  line-height: clamp(1.1em, 2.2vw, 1.6em);
}