*, *::after, *::before {box-sizing:border-box;margin:0;padding:0}
html, body, main       {position:relative;font-family:'Noto Sans',sans-serif; background-color: var(--primary-background-color);
                        color:var(--primary-font-color, hsl(0, 0%, 25%))}
body                   {overflow-x:hidden;overflow-y:scroll;display:flex;min-height:100vh;flex-direction:column}
a                      {text-decoration:none;color:var(--primary-color)}
img                    {max-width:100%;height:auto}
.icon                  {width:1em;height:1em;vertical-align:-.15em;fill:currentColor;overflow:hidden}
.shadow                {box-shadow:0 1px 3px hsl(0, 0%, 10%), 0 1px 2px hsl(0, 0%, 25%)!important}
input:focus            {border:none; outline:none;}

@import "grid";
@import "typography";

@import "components/header";
@import "components/search-results";
