
#search_results         {position:absolute;top:0;left:0;right:0;bottom:0;background:#fff;z-index:99;display:flex;
                        color:#222;padding:0 30px;justify-content:space-between;overflow:scroll;border: thin solid black;
}
#search_results:empty   {display:none}
#search_results a       {}
#search_results a b     {color:#48b}
#search_results h2      {font-size:.8rem;padding-top:16px;margin:16px 0 9px 0;text-transform:uppercase;text-align:center;border-bottom: thin solid #222}
#search_results h2 svg  {height:24px;width:28px;display:inline-block;margin:0 12px -5px 0}
#search_results .lang   {position:relative;bottom:-22px;float:right;font-size:.8rem}

#search_results div[data-count="0"] {display: none}

#search_results a    {font-size:1rem;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;}
#search_results .row {max-width: 100vw;width: 100%}

#search_results section {
  display: flex;
  flex-direction: column;
}

