#banner
{background-color:var(--banner-background);color:var(--banner-text);position:relative;
  display:flex;justify-content:center;flex-flow:column;border:thin solid #ddd;border-bottom:none;}

#banner .banner-image
{position:absolute;left:0;top:0;right:0;bottom:0;width:100%;height:auto;z-index:1}

#banner .banner-heading
{padding:1.5rem 0 .5rem 2rem;}

#banner .banner-image img
{object-fit:cover;height:100%;width:100%;}

#banner.gradient:after
{content:"";position:absolute;left:0;right:0;top:0;height:100%;width:100%;
  background:linear-gradient(to right, rgba(0, 0, 0, 0.25) 20%, rgba(0, 0, 0, 0.5) 52%, rgba(0, 0, 0, 0) 100%);}

#banner h1, #banner h2
{letter-spacing:0.05em;font-weight:400;z-index:5;position:relative;text-shadow:2px 2px 3px rgba(0, 0, 0, 0.4);
  color:#fff;line-height:clamp(1.3rem, 4vw, 1.7rem);}

#banner .banner-title
{margin:1em auto;text-align:center;}

#banner h1
{font-size:clamp(1.1rem, 3vw, 1.5rem); text-transform:uppercase;}

#banner h2
{margin:0;padding:0;text-align:left;font-size:clamp(.85rem, 4vw, 1rem);}

#banner nav a.active
{text-decoration:underline}

#banner .banner-flag
{height:3.2rem;width:auto;margin:1.2rem 1rem 0 1.5rem;position:relative;z-index:2;
  box-shadow:2px 2px 2px 1px rgba(0, 0, 0, 0.2);}

.banner-icon
{height:2.5em;width:2.5em;z-index:4;position:relative;color:#fff;
  -webkit-filter:drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));filter:drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));}

.banner-button
{position:absolute;top:5px;background-color:#f2f2f2;background-repeat:no-repeat;background-position:center;
  background-size:16px 16px;height:1.8rem;width:1.8rem;border:solid 1px #ccc;border-radius:100%;
  cursor:pointer;z-index:3}

input.banner-button
{background-image:url('/img/search_glass.svg');outline:none;text-indent:-9999px;
  transition-timing-function:ease-in;transition-duration:.2s;right:70px;}

input.banner-button.focus,
input.banner-button:focus
{justify-content:center;background-position:1rem center;border-radius:12px;z-index:12;
  text-indent:3rem;margin-left:-340px;width:360px;outline-style:none;box-shadow:none;
  border-color:transparent;z-index:99}

#i18n_wrap_open
{right:5px;background-image:url("/img/nav_language.svg");}

#page-info
{right:37px}

#page-info svg
{width:100%;height:100%;border-radius:100px;margin:0 auto;fill:#666;padding:6px;}

#i18n_wrap nav
{display:none;width:100%;}

#i18n_wrap.active
{position:absolute;width:100%;height:100%;background:var(--primary-color);z-index:12;color:#fff;}

#i18n_wrap.active nav
{display:flex;place-items:center;justify-content:center;flex-wrap:wrap;}

#i18n_wrap.active nav a
{color:#fff;padding:1em 0;width:20%;text-align:center;}

#i18n_wrap_close
{color:#222;background-color:#f7f7f7;width:22px;height:22px;text-align:center;border:thin solid #ccc;
  border-radius:12px;font-size:14px;padding-top:2px;position:absolute;right:12px;top:6px;}

#breadcrumbs
{font-size:clamp(.7rem, 2vw, .8rem);color:var(--primary-color);margin:1em 0 1em 1em;letter-spacing:0.025em;
  text-transform:uppercase;transition:all 0.27s cubic-bezier(0, 0, 0.58, 1);}

#breadcrumbs a
{color:var(--primary-color);}

#breadcrumbs a span::after
{content:"/";margin:0 6px;color:#999;}

.banner-country
{height:4.5rem;width:auto;margin:.5rem 1rem 0 1.5rem}

