.modal
{position:fixed;left:0;top:0;width:100%;height:100%;background-color:rgba(0, 0, 0, 0.5);opacity:0;visibility:hidden;
  transform:scale(1.1);transition:visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;z-index:25;}

.modal-content
{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background-color:white;padding:1rem 1.5rem;width:24rem;border-radius:0.5rem;}

.close-button
{float:right;width:1.5rem;line-height:1.5rem;text-align:center;cursor:pointer;border-radius:0.25rem;background-color:lightgray;}


.close-button:hover
{background-color:darkgray;}

.show-modal
{opacity:1;visibility:visible;transform:scale(1.0);transition:visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;}


/* Map Modals  */

.map-modals .modal-content
{background-color:rgba(0, 0, 0, 0.4); height:100%; width:100%;border-radius:0; max-height:none;}

.map-modals .modal-content img
{display: block; height:95vh; margin: 0 auto;}

.map-modals .modal-close
{float:right;width:3.5em;height: 3em;line-height:1rem;text-align:center;cursor:pointer;border-radius:0.25rem;background-color:#222;
  text-transform: uppercase;padding:.3em;color:#ddd;font-size:.9em;}

.modal-close
{padding: .5em; border: thin solid #d7d7d7; margin: .2em; text-transform: uppercase; position:absolute; right: 15px;}

/* Page Info Modals  */

.modal-info
{padding: 1em;}

.modal-info p
{font-size: .82em; line-height: 1.2em; margin: .5rem 0;}

.modal-info h3
{font-size: .9em; text-decoration: underline; margin: .5em 0; text-transform: uppercase;text-align: left; font-weight: normal;}

.modal-info li
{font-size: .8em; line-height: 1.1em; margin: .2em 2.5em;}

.modal-info a.help
{text-align: right; padding: .5em 3em 0 0; height:30px; display:block; background: url("https://images.bible.cloud/fab/fab_color_faq.svg") no-repeat;
background-size:30px 30px; background-position: right top;}