#maps h3  {text-align: center; padding:1em 0 1em 0;}
#maps  hr {overflow: visible; /* For IE */ padding: 0; border: none; border-top: medium double #ccc; text-align: center; margin:2em 10% 1em 10%;}
#world img      {display: block;margin: 0 auto}

.map-section img {margin: 0 auto; display: block;}
.map-img        {display:flex; flex-direction: column; justify-content: center;}
.map-pdf        {display:block; text-align: center; margin: .5em auto;width: 112px; height: 30px; padding: 3px 0 0 20px;
                 background:url("http://images.bible.cloud/icon/pdf_icon.svg") no-repeat 0px 0px/ 20px 30px; }

.map-size       {font-size: .7rem; color: #999; margin-left: .3rem; }
.modal-content  {width:90%; max-height:90%}

.map-modals .modal-content {background-color: transparent;}
.map-modals .modal-content img {display: block;height: 80vh;margin: 0 auto;}

.map-radius     {border-radius: 4em;}
