/* Language Aside (Wikipedia) */

.infobox       {font-size: .7em;margin: 0 1.5em; padding: 0 .5em;background-color: #fff; line-height: 1.2em;}
.infobox tbody {vertical-align:middle;border-color: inherit;}
.infobox tr    {vertical-align:text-top;}
.infobox th    {vertical-align:inherit;font-weight:bold;border-top: 1em solid transparent;border-bottom: 1em solid transparent;}
.infobox ul    {display: block;list-style-type: disc;}
.infobox li	   {display: list-item;}

.lang-title                            {text-transform: uppercase; font-size:1.4em; color: #000;}
.lang-section, .lang-desc              {text-align: center; color:#222; font-size:1.2em;}
.plainlist ol, .plainlist ul           {list-style:none;}

.mw-empty-elt                          {display:none;}
code                                   {font-family: inherit;font-size: 1em;}
.link-container                        {display:flex;justify-content: space-around}
.link-container img                    {width:50px;}
